.typewriter__content {
    display: flex;
    align-items: center;
    justify-content: center;
    position: absolute;
    height: 80vh;
    top: -5vh;
}

.typewriter__content span.img-txt {
    text-transform: uppercase;
    color: black;
    padding: 1rem;
    font-size: 4rem;
    font-weight: bold;
    letter-spacing: 10px;
    text-align: center;
}

@media screen and (max-width: 1200px) {
    .carousel__content {
        display: none;
    }

    .typewriter__content {
        max-width: 80%;
    }
}
